
import XHeader from 'vux/src/components/x-header/index.vue'
import InlineLoading from 'vux/src/components/inline-loading/index.vue'

export default {
components: {
XHeader,
InlineLoading
},

beforeRouteLeave(to, from, next){
if (to.name == 'addMedicalCard' || to.name == 'visitCardDetails') {
window.localStorage.setItem('jzrInfo',JSON.stringify(this.jzrInfo));
}else{
window.localStorage.removeItem('jzrInfo');
}
next();
},
data() {
return {
noData: false,

jzrInfo:  {},
list: [],
};
},
mounted() {

this.jzrInfo = this.$route.params.jzrInfo || JSON.parse(window.localStorage.getItem('jzrInfo')) || {};


this.getVisitCardList();
},
methods: {

getVisitCardList() {
this.$vux.loading.show({
text: '加载中...'
})
this.$ajax.post('',{
"MSH.1": "visitcard",
"MSH.2": "pageVisitCard",
"token": this.$root.loginInfo.token,
"jzrBm": this.jzrInfo.CBM,
"pageIndex": 1,
"pageSize": 100,
}).then(res => {
console.log(res);
this.$vux.loading.hide();
this.list = [];
if(typeof res != 'undefined' && typeof res.rows != 'undefined') {
if (res.rows.length > 0) {
this.noData = false;
this.list = res.rows;
} else {
this.noData = true;
}
} else {
this.noData = true;
}
}).catch(error => {
console.log(error);
this.$vux.loading.hide();
this.noData = true;
})
},

gotoDetail(CBM) {
this.$router.push({name: "visitCardDetails", params: {CBM: CBM}});
},

deleteVisitCard(CBM) {
this.$vux.loading.show({
text: '加载中...'
})
this.$ajax.post('',{
"MSH.1": "visitcard",
"MSH.2": "delVisitCard",
"token": this.$root.loginInfo.token, //token
"cbm": CBM,
}).then(res => {
console.log(res);
this.$vux.loading.hide();
if (typeof res.status != 'undefined' && res.status == 1) {
this.$vux.toast.show({
text: '解绑成功!',
type: 'success'
});

this.getVisitCardList();
}
}).catch(error => {
console.log(error);
this.$vux.loading.hide();
})
},
relieve(CBM) {
const _this = this; 
this.$vux.confirm.show({
content: '解绑后， 将不能通过该卡号使用在线缴费、预约挂号了，确定解绑',

onCancel() {
},
onConfirm() {
_this.deleteVisitCard(CBM);
}
});
}
}
};



